<template>
  <div >
      <Dialog v-if="logOutDialogDisplay" title="退出确认" @cancel="cancelClick" @confirm="confirmClick" description="确定要导出所选的文件吗？" :isDisplay.sync="logOutDialogDisplay"></Dialog>
  </div>
</template>

<script>
import Dialog from "components/Dialog.vue"

export default {
  components:{
      Dialog
  },
  data(){
      return {
          logOutDialogDisplay:false,
      }
  },
  mounted(){
      this.logOutDialogDisplay = true;
  },
  methods:{
      cancelClick(){
          /* this.logOutDialogDisplay = false; */
          this.$router.go(-1);
      },
      confirmClick(){
          this.$router.push('./login')
      }
  }
}
</script>

